* {
  margin: 0;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  height: 60px;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.logo__img {
  display: block;
}

.logo {
  margin: 10px 0;
}

.nav__list {
  align-items: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.is-active, .shows__active:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

